.fadeInOut-enter {
  opacity: 0;
}
.fadeInOut-enter-active {
  opacity: 1;
  -webkit-transition: opacity 2000ms;
  -o-transition: opacity 2000ms;
  transition: opacity 2000ms;
}
.fadeInOut-leave {
  opacity: 1;
}
.fadeInOut-leave-active {
  opacity: 0;
  -webkit-transition: opacity 2000ms;
  -o-transition: opacity 2000ms;
  transition: opacity 2000ms;
}

.content {
  position: relative;
}

.comp {
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
}



